import React, { useEffect, useState } from "react";
import { useStore } from "../src/store/store";
import Header from "./components/header/header";
import CenteredTabs from "./components/tabs/tabs";
import LoginScreen from "./components/loginScreen/loginScreen";
import Notification from "./components/notification/notification";
import socketIOClient from "socket.io-client";
import { BASE_URL } from "./variables";

function App() {
  const userId = useStore((state) => state.userId);
  const [userID, setUserID] = useState(userId);
  const updateUserId = useStore((state) => state.updateUserId);
  const [isLogin, setIsLogin] = useState(false);
  const [openNotif, setOpenNotif] = useState(false);

  const msgData = useStore((state) => state.msgData);
  const updateMsgData = useStore((state) => state.updateMsgData);

  const [msg, setMsg] = useState(null);

  const handleMessage = (newMsg) => {
    setMsg(newMsg);
  };

  useEffect(() => {
    // Connect to the Socket.IO server
    const socket = socketIOClient(BASE_URL);

    socket.emit("login", userId);

    // Listen for 'message' events
    socket.on("new_request", (data) => {
      console.log("-----new_request notification--------", data);
      setMsg(`Request ${data}`);
      // Update state with received message
      updateMsgData(`Request ${data}`);
    });

    socket.on("update_request", (data) => {
      console.log("-----update_request notification--------", data);
      setMsg(`Request update ${data}`);
      // Update state with received message
      updateMsgData(`Request update ${data}`);
    });

    socket.on("new_reservation", (data) => {
      console.log("-----new_reservation notification--------", data);
      setMsg(`Reservation ${data}`);
      // Update state with received message
      updateMsgData(`Reservation ${data}`);
    });

    socket.on("update_reservation", (data) => {
      console.log("-----update_reservation notification--------", data);
      setMsg(`Reservation status ${data}`);
      // Update state with received message
      updateMsgData(`Reservation status ${data}`);
    });

    // Clean up the socket connection when the component unmounts
    return () => {
      socket.disconnect();
    };
  }, [userId]); // empty dependency array ensures this effect runs only once

  const handleCloseNotification = () => {
    setOpenNotif(false);
    setMsg(null);
  };

  return (
    <div className="App">
      {userId && (
        <Notification
          handleCloseNotification={handleCloseNotification}
          message={msg}
        />
      )}
      {!userId ? <LoginScreen handleMessage={handleMessage} /> : <Header />}
    </div>
  );
}

export default App;
